body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left, rgb(0, 71, 244), rgb(255, 76, 121));
  background: linear-gradient(to right, rgb(0, 71, 244), rgb(255, 76, 121));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

button {
  cursor: pointer;
}

.App {
  text-align: center;
}

.center{
  display: flex;
  justify-content: center;
}

.particles{
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
}
.Tilt{
    background: -webkit-linear-gradient(left, rgb(0, 71, 244), rgb(255, 76, 121));
    background: linear-gradient(to right, rgb(0, 71, 244), rgb(255, 76, 121));
}
.form{
    width: 700px;
    background: -webkit-linear-gradient(45deg, #dca 12%, transparent 0, transparent 88%, #dca 0),
    -webkit-linear-gradient(315deg, transparent 37%, #a85 0, #a85 63%, transparent 0),
    -webkit-linear-gradient(45deg, transparent 37%, #dca 0, #dca 63%, transparent 0) #753;
    background: linear-gradient(45deg, #dca 12%, transparent 0, transparent 88%, #dca 0),
    linear-gradient(135deg, transparent 37%, #a85 0, #a85 63%, transparent 0),
    linear-gradient(45deg, transparent 37%, #dca 0, #dca 63%, transparent 0) #753;
    background-size: 25px 25px;
}

.center{
    display: flex;
    justify-content: center;
}
